/**
 * @packageDocumentation
 * @module Serializer
 */

import createMoPlugins from './createMoPlugins';
import createSerializer from './createSerializer';
import { MoOptions } from './type';
/**
 * Serializer，序列化工具
 */
function Mo(options?: MoOptions) {
  const plugins = options ? options.plugins : createMoPlugins();
  return createSerializer(plugins, options?.parser);
}

export const moSerializer = Mo({ plugins: createMoPlugins() });

export default Mo;
